import React, { useState } from "react";

import Header from "./Header";
import { PageContainer, Page } from "./UniversalStyle";
import BackgroundImage from "../images/background5.jpg";

import { Title } from "./UniversalStyle";
import { 
  MiniNavBar, SelectRuleButton, 
  Rule, RuleNumber, RuleText, RulesContent,
  Quote, Spoiler, SpoilerButton, SpoilerContainer 
} from "./RulesStyle";

import PROJson from "./RulesCNN/PRO.json";
import PPGJson from "./RulesCNN/PPG.json";
import PPEJson from "./RulesCNN/PPE.json";
import PPAJson from "./RulesCNN/PPA.json";
import EYMJson from "./RulesCNN/EYM.json";

function ParseQuote(quote)
{
  return (
    <Quote>
      {
        quote.map( (text, index) => {
          return(<div key={index}>{text}</div>);
        })
      }
    </Quote>
  );
}

function ParseSpoiler(props)
{
  const [isOpen, Open] = useState(false);

  function ToggleOpen()
  {
    Open(!isOpen);
  }

  return (
    <Rule>
      <SpoilerButton active={isOpen} onClick={() => ToggleOpen()}>{props.title}</SpoilerButton>
      <SpoilerContainer showDropdown={isOpen}>
        {
          <Spoiler>
          {
            props.content.map( (text, index) => {
              return(<div key={index}>{text}</div>);
            })
          }
          </Spoiler>
        }
      </SpoilerContainer>
    </Rule>
  );
}

function ParseJSON(json)
{
  return (
    <div>
      {
        json.map( (section) => {
          return (
            <div key={section.chapter}>
              <Title style={{marginBottom: "5px"}}> 
                { section.chapter + " " + section.desc} 
              </Title>
              {
                section.content.map( (rule, index) => {
                  return (
                    <Rule key={index}>
    
                      <RuleNumber>
                        {rule.number + " "}
                        <RuleText>{rule.text}</RuleText>
                      </RuleNumber>
                      
                      { rule.quote ? ParseQuote(rule.quote) : null }
                      { rule.spoiler ? (<ParseSpoiler title={rule.spoiler.title} content={rule.spoiler.content}></ParseSpoiler>) : null }
    
                    </Rule>
                  );
                })
              }
            </div>
          )
        })
      }
    </div>
  )
}

var RuleMap = new Map();
RuleMap.set("–ü–†–û", ParseJSON(PROJson));
RuleMap.set("–ü–ü–≠", ParseJSON(PPGJson));
RuleMap.set("–ü–ü–ê", ParseJSON(PPEJson));
RuleMap.set("–ü–ü–ì", ParseJSON(PPAJson));
RuleMap.set("–ï–£–ú", ParseJSON(EYMJson));

function RulesCNN() 
{
  const [selectedSection, SelectSection] = React.useState("–ü–†–û");

  return (
    <div>
      <Header background={BackgroundImage} title={"–ü—Ä–∞–≤–∏–ª–∞ –°–ú–ò"}></Header>
      <PageContainer>
        <Page>
          <MiniNavBar>
            <SelectRuleButton section={"–ü–†–û"} onClick={SelectSection} selectedSection={selectedSection}>
              –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –æ–±—ä—è–≤–ª–µ–Ω–∏—è
            </SelectRuleButton>
            <SelectRuleButton section={"–ü–ü–≠"} onClick={SelectSection} selectedSection={selectedSection}>
              –≠—Ñ–∏—Ä–æ–≤
            </SelectRuleButton>
            <SelectRuleButton section={"–ü–ü–ê"} onClick={SelectSection} selectedSection={selectedSection}>
              –ê—É–¥–∏–æ-–≠—Ñ–∏—Ä–æ–≤
            </SelectRuleButton>
            <SelectRuleButton section={"–ü–ü–ì"} onClick={SelectSection} selectedSection={selectedSection}>
              –ü—Ä–æ–¥–∞–∂–∞ –∏ –ø–µ—á–∞—Ç—å –≥–∞–∑–µ—Ç
            </SelectRuleButton>
            <SelectRuleButton section={"–ï–£–ú"} onClick={SelectSection} selectedSection={selectedSection}>
              –£—Å—Ç–∞–≤ –º–µ–¥–∏–∞–∫–æ—Ä–ø–æ—Ä–∞—Ü–∏–∏
            </SelectRuleButton>
          </MiniNavBar>
          <RulesContent>

              { RuleMap.get(selectedSection) }

          </RulesContent>
        </Page>
      </PageContainer>
    </div>
  );
}

export default RulesCNN;